html, body {
  margin: 0;
  padding: 0;
}

.nav{
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 10px;
}

.nav-logo {
  width: 60px;  
}

.nav-items {
  list-style: none;
  display: flex;
}

.nav-items > li {
  padding: 10px;  
}

.footer {
  display: flex;
  background-color: #dcdada;
  position: absolute;
  bottom: 0;
  font-size: 50px;
}